// @import "node_modules/bootstrap-sass/assets/stylesheets/bootstrap";

body {
	background-color: #D3D3D3;
	color: black;
	margin-bottom:100px;
}

.red, h1, h2, h3, h4 {
	color: #E31E19;
}

#heading h5, h5, h4, h4 { 
	display: inline; 
}

// Navigation Bar and Footer

	.navbar {
		background-color: black;
		@media screen and (max-width: 770px) {
			padding-bottom: 40px;
	}
	}

	.navbar-inverse, .nav.navbar-nav li a {
	   	color: #E31E19;
	   	@media screen and (min-width: 770px) {
	   	line-height: 70px;
	}
	}

	.navbar-brand {
		margin-right: 15px;
	}

	@media screen and (max-width: 750px) {
	#myNavbar {
		margin-top: 45px;
	}
	}

	#myDropdown {
		line-height: 20px;
	}

	footer {
		position: relative;
	    bottom: -20px;
	    margin-bottom: -100px;
	}

	.footer-copyright-padding {
	padding-right: 20px;
	}

// API's and Embeds

	#social_share_buttons { 
		display: flex; 
		justify-content: center; 
	}

	#map {
	    width: 100%;
	    height: 400px;
	    background-color: grey;
	}

	p > iframe {
	  width: 100%    !important;
	  height: 450px   !important;
	}

// Spacing

	.login-register-spacing {
		margin-top: 20px;
	}

	.login-register-or-spacing {
		margin: 0px 0 10px 0;
	}

	.btn-h1-spacing {
		margin-top: 15px;
	}

	.form-spacing-top, .edit-box-spacing  {
		margin-top: 25px;
	}

	@media screen and (min-width: 750px) {
	.contact-box-spacing {
		margin-top: 80px;
	}
	}

	@media screen and (min-width: 750px) {
	.location-box-spacing {
		margin-top: 50px;
	}
	}

// Blog Specific SCSS

	.post-body-padding {
		padding: 15px 0 15px 0;
	}

	.single-post-body-padding {
		padding: 5px 0 15px 0;
	}

	.blog-title-image-spacing {
		display: inline-block;
		vertical-align: middle;
	}

	.blog-image-spacing {
		padding-right: 10px;
	}

	@media screen and (max-width: 750px) {
	.blog-image-spacing > img {
		height: 200px; 
		width: 350px;
		width: 100%;
	}
	}

	@media screen and (min-width: 750px) {
	#post-title-spacing {
		margin: -10px 0 -5px 0;
	}
	}

	.blog-index-sidebar {
		height: 100%;
		background-color: #E9E9E9;
		margin-top: 25px;
		padding: 0 10px 0 25px;
		border-radius: 25px;
	}

	@media screen and (min-width: 750px) {
	.blog-index-sidebar-content {
		padding: 50px 14px 25px 14px;
	}
	}

	.blog-index-sidebar-contact {
		padding: 25px 14px 0px 12px;
	}

		// Search Button SCSS

			.search-input-padding {
				padding-top: 0;
				margin-top: 0;
				margin-bottom: 15px;
			@media screen and (min-width: 750px) {
				margin-bottom: 25px;
			}
			}

			#custom-search-input {
		        margin:0;
		        margin-top: 10px;
		        padding: 0;
		    }
		 
		    #custom-search-input .search-query {
		        padding-right: 15px;
		        padding-right: 4px \9;
		        padding-left: 10px;
		        padding-left: 4px \9;
		        /* IE7-8 doesn't have border-radius, so don't indent the padding */
		 
		        margin-bottom: 0;
		        -webkit-border-radius: 3px;
		        -moz-border-radius: 3px;
		        border-radius: 3px;
		    }
		 
		    #custom-search-input button {
		        border: 0;
		        background: none;
		        /** belows styles are working good */
		        padding: 2px 5px;
		        margin-top: 2px;
		        position: relative;
		        left: -28px;
		        /* IE7-8 doesn't have border-radius, so don't indent the padding */
		        margin-bottom: 0;
		        -webkit-border-radius: 3px;
		        -moz-border-radius: 3px;
		        border-radius: 3px;
		        color:#D9230F;
		    }
		 
		    .search-query:focus + button {
		        z-index: 3;   
		    }

		//Comment System SCSS

			.comment {
				margin-bottom: 45px;
			}

			#comment-children {
				margin-top: 25px;
			}

			#comment-form {
				margin-top: -20;
			}

			.author-image {
				width: 50px;
				height: 50px;
				border-radius: 50%;
				float: left;
			}

			.author-name {
				float: left;
				margin-left: 15px; 
			}

			.author-name>h4 {
				margin: 5px 0px;
				
			}

			.author-time {
				font-size: 12px;
				font-style: italic;
				color: #aaa;
			}

			.comment-content {
				clear: both;
				margin-left: 65px;
				margin-botom: 25px;
				font-size: 16px;
				line-height: 1.3em;
			}

			.comments-title {
				margin-bottom:45px;
				margin-top: 10px;
			}

			.comments-title>span {
				margin-right: 15px;
			}

			.glyphicon-comment {
				margin-right: 5px !important;
			}

// Photos Page SCSS

	@media screen and (min-width: 750px) {
	.carousel {
		width: 100%;
	    height: 625px;
	    padding: 10px 70px;
	}
	}

	@media screen and (min-width: 750px) {
	.carousel-inner > .item > img {
	    margin: 0 auto;
	}
	}

	@media screen and (max-width: 750px) {
	.carousel-inner > .item > img, .carousel-inner > .item > a > img {
		width: 100%;
		margin: auto;
	}
	}

	@media screen and (max-width: 750px) {
	img {
		width: 100%;
		margin: auto;
	}
	}


// Tables

	.table, th, thead { border: 1px solid #000000; 
		text-align: center;
	} 

	.table-bordered > thead > tr > th,
	.table-bordered > tbody > tr > th,
	.table-bordered > tfoot > tr > th,
	.table-bordered > thead > tr > td,
	.table-bordered > tbody > tr > td,
	.table-bordered > tfoot > tr > td { 
		border: 1px solid #000000; 
	}

	.table tbody tr:hover td, .table tbody tr:hover th {
	    background-color: #E31E19;
	}